
header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container{
    height: 100%;
    text-align: center;
    position: relative;  
}


/* =========== CTA =============== */
.cta{
    margin-top: 2rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials{
    display: flex;
    flex-direction: column;
    align-items:center;
    gap: 1.2rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.header__socials:after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary)
}

/* =============== Me =================== */

.me{
    background: linear-gradient(var(--color-primary),transparent);
    position: absolute;
    width: 22rem;
    height: 30rem;
    margin-top: 5rem;
    border-radius: 12rem 12rem 0 0;
    left: calc(50% - 11rem);
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem; 
}

/* =============== Scroll down ================ */
.scroll__down{
    position: absolute;
    right: -2rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;    
}

/* ----------------- Media queries (Medium devices) --------------- */
@media (max-width: 1024px){
    header{
        height: 68vh;
    }
}
/* ----------------- Media queries (Small Devices) --------------- */
@media (max-width: 600px){
    header{
        height: 100vh;
    }
    .header__socials,.scroll__down{
        display: none;
    }
}